<template>
    <div class="form-container">
        <div class="tit">为您推荐</div>
        <div class="accordion">
            <el-collapse accordion>
                <el-collapse-item
                    v-for="item in $store.state.fitting.newDataObj"
                    :key="item.id"
                >
                    <template slot="title">
                        <span>{{ item.pinpai + " " + item.xinghao }}</span>
                    </template>
                    <div class="information">
                        <div><strong>涂装：</strong>{{ item.tuzhuang }}</div>
                        <div><strong>价格：</strong>{{ item.jiage }}</div>
                        <div>
                            <strong>推荐尺码：</strong
                            >{{ item.size }}
                        </div>
                        <div>
                            <el-button size="medium" @click="more(item.lianjie)"
                                >更多详情</el-button
                            >
                        </div>
                    </div>
                </el-collapse-item>
            </el-collapse>
        </div>
        <div class="return">
                <el-button icon="el-icon-back" circle @click="ret"></el-button>
        </div>
    </div>
</template>

<script>
    export default {
        name: "StepFirst",
        methods: {
            more(value) {
                window.open(value);
            },
            ret(){
                this.$router.push({
                    name: "stepthird",
                });
            }
        },
        activated() {
            this.$store.state.fitting.nowStep.isSecond = true;
            this.$store.state.fitting.nowStep.isThird = true;
            this.$store.state.fitting.nowStep.isFourth = true;
        },
    };
</script>

<style scoped lang="less">
    .accordion {
        width: 85%;
        margin: auto;
        span {
            font-size: 18px;
        }
        .information {
            position: relative;
            font-size: 16px;
            div {
                margin-left: 30px;
            }
            div:nth-child(4) {
                position: absolute;
                right: 50px;
                top: 42px;
            }
        }
    }
</style>