<template>
    <div class="form-container">
        <div class="tit">请填写您的身体数据</div>
        <form action="/productApp/form" method="post">
            <div style="width: 464px; margin-left: 122px">
                <label for="shengao" style="margin-top: 25px">身高</label>
                <span style="vertical-align:middle">/cm</span>

                <el-slider
                    v-model="$store.state.fitting.dataObj.shengao"
                    :step="1"
                    :min='150'
                    :max="190"
                    show-input
                >
                </el-slider>
            </div>

            <div style="width: 464px; margin-left: 122px">
                <label for="kuagao" style="margin-top: 25px">跨高</label>
                <span style="vertical-align:middle">/cm</span>

                <el-slider
                    v-model="$store.state.fitting.dataObj.kuagao"
                    :step="1"
                    :min='60'
                    :max="90"
                    show-input
                >
                </el-slider>
            </div>

            <div class="btn">
                <el-button
                    type="success"
                    plain
                    @click="submit"
                    v-show="isShownext"
                    style="margin-left: 0"
                    >提交信息</el-button
                >
                <el-button type="success" v-show="isloading" :loading="true"
                    >加载中</el-button
                >
            </div>
            <div class="return">
                <el-button icon="el-icon-back" circle @click="ret"></el-button>
            </div>
        </form>
    </div>
</template>

<script>
    import axios from "axios";
    export default {
        name: "StepFirst",
        data() {
            return {
                that: this,
                errMsg: "",
                isloading: false,
                isShownext: true,
            };
        },
        methods: {
            err() {
                this.$notify.error({
                    title: "错误",
                    message: `${this.errMsg}：不能为空！`,
                });
            },
            ret() {
                this.$router.push({
                    name: "stepsecond",
                });
            },
            submit() {
                this.isShownext = false;
                this.isloading = true;
                axios
                    .post("http://127.0.0.1:8000/getData", {
                        fenlei: this.$store.state.fitting.dataObj.fenlei,
                    })
                    .then((response) => {
                        this.$store.state.fitting.resData = response.data;
                        this.$router.push({
                            name: "stepfourth",
                        });
                        this.newData();
                    })
                    .catch((error) => {
                        alert("请求失败，错误信息：" + error);
                        this.isloading = false;
                        this.isShownext = true;
                    });
            },
            newData() {
                // 定义一个变量用来存放国内牌子
                const localBrand = ["Giant", "Kung", "Xds"];

                // 山地车尺码适配表
                const MountainSize = {
                    XS: [148, 157],
                    S: [157, 165],
                    M: [165, 178],
                    L: [178, 185],
                    XL: [185, 193],
                    XXL: [193, 200],
                };

                // 城市车尺码适配表
                const FitnessSize = {
                    XS: [145, 155],
                    S: [155, 165],
                    M: [165, 175],
                    L: [175, 183],
                    XL: [183, 193],
                    XXL: [193, 200],
                };

                // 每项得分
                let itemScore = {};

                // rgb转换
                function hexToRgb(hexValue) {
                    var rgx = /^#?([a-f\d])([a-f\d])([a-f\d])$/i;
                    var hex = hexValue.replace(rgx, function (r, g, b) {
                        return r + r + g + g + b + b;
                    });
                    var rgb = /^#?([a-f\d]{2})([a-f\d]{2})([a-f\d]{2})$/i.exec(hex);
                    if (!rgb) {
                        return hexValue;
                    }
                    var r = parseInt(rgb[1], 16);
                    var g = parseInt(rgb[2], 16);
                    var b = parseInt(rgb[3], 16);
                    return [r, g, b];
                }
                // 将用户输入的涂装转换为rgb
                let userRgb = hexToRgb(this.$store.state.fitting.dataObj.tuzhuang);
                // 处理跨高身高
                const kuagao = parseInt(this.$store.state.fitting.dataObj.kuagao);
                const shengao = parseInt(this.$store.state.fitting.dataObj.shengao);
                // 山地车尺码表涵盖

                this.$store.state.fitting.resData.forEach((item) => {
                    // 品牌得分

                    let brand_score;
                    // 判断用户输入的品牌是否和此项品牌相同
                    if (this.$store.state.fitting.dataObj.pinpai == item.pinpai) {
                        brand_score = 100;
                    } else {
                        // 判断用户输入的是否是国内品牌
                        if (
                            localBrand.includes(
                                this.$store.state.fitting.dataObj.pinpai
                            )
                        ) {
                            // 判断该项是否是国内品牌
                            if (localBrand.includes(item.pinpai)) {
                                // 如果都是国内品牌,则分数为60
                                brand_score = 60;
                            }
                            // 如果用户输入的是国内品牌,而此项不是国内品牌
                            else brand_score = 60;
                        } else {
                            // 用户输入的是国外品牌
                            // 判断此项是否是国内品牌
                            if (localBrand.includes(item.pinpai)) {
                                // 如果是国内品牌,则分数为30
                                brand_score = 30;
                            } else {
                                // 此项是国外品牌,和用户输入同属国外品牌
                                brand_score = 60;
                            }
                        }
                    }

                    // 涂装得分

                    // 将此项的涂装信息转换为数组
                    let ItemColorArr = item.tuzhuang.split(" ");
                    // 定义对象用于存储每项中每个涂装与用户输入涂装的相似度
                    let colorEgItemObj = {};
                    ItemColorArr.forEach((item, index) => {
                        let rgb = hexToRgb(item);
                        // 计算rgb欧氏距离
                        const colorEG = Math.sqrt(
                            Math.pow(rgb[0] - userRgb[0], 2) +
                                Math.pow(rgb[1] - userRgb[1], 2) +
                                Math.pow(rgb[2] - userRgb[2], 2)
                        );
                        colorEgItemObj[index] = colorEG;
                    });
                    // colorEgItemObj value相似度升序,计算出minColorItemEg 每项相似度最高的颜色
                    const minColorItemEg = Object.values(colorEgItemObj).sort(
                        (a, b) => {
                            return a - b;
                        }
                    );
                    // 计算涂装得分
                    let color_score = Math.round(10000 / minColorItemEg[0]);

                    // 价格得分
                    let price_score;
                    let itemPrice = Math.round(item.jiage);
                    // 判断此项价格是否在用户预算内
                    if (
                        itemPrice > this.$store.state.fitting.dataObj.jiage[0] &&
                        itemPrice < this.$store.state.fitting.dataObj.jiage[1]
                    ) {
                        // 在预算区间内,得分100
                        price_score = 100;
                    } else {
                        // 不在预算区间内,判断在左还是在右
                        let dleft = Math.abs(
                            itemPrice - this.$store.state.fitting.dataObj.jiage[0]
                        );
                        let dright = Math.abs(
                            itemPrice - this.$store.state.fitting.dataObj.jiage[1]
                        );
                        // 若距左小于距右,则此项价格低于预算
                        if (dleft < dright) {
                            price_score = Math.round(
                                ((this.$store.state.fitting.dataObj.jiage[1] -
                                    this.$store.state.fitting.dataObj.jiage[0]) /
                                    (this.$store.state.fitting.dataObj.jiage[1] -
                                        itemPrice)) *
                                    100
                            );
                        } else {
                            price_score = Math.round(
                                ((this.$store.state.fitting.dataObj.jiage[1] -
                                    this.$store.state.fitting.dataObj.jiage[0]) /
                                    (itemPrice -
                                        this.$store.state.fitting.dataObj
                                            .jiage[0])) *
                                    100
                            );
                        }
                    }
                    // 尺码得分
                    let size_score;
                    // 将此项的尺码信息转换为数组
                    let ItemSizeArr = item.mashu.trim().split(" ");
                    // 此项推荐尺码
                    let recSize;
                    // 此项车辆最小尺码
                    let ItemSizeMin = ItemSizeArr[0];
                    // 此项车辆最大尺码
                    let ItemSizeMax = ItemSizeArr[ItemSizeArr.length - 1];
                    let ItemSizeIMILeft;
                    let ItemSizeIMIRight;
                    let flag;

                    // 判断车类型
                    if (
                        this.$store.state.fitting.dataObj.fenlei == "TT bike" ||
                        this.$store.state.fitting.dataObj.fenlei == "Road bike"
                    ) {
                        // 推荐尺码
                        recSize = Math.round(0.66 * kuagao + 2);
                        // 公路车计算方法
                        // 若此项车辆尺寸区间涵盖推荐尺码则得分100
                        if (recSize > ItemSizeMin && recSize < ItemSizeMax) {
                            size_score = 100;
                        } else {
                            // 不在尺码区间内,判断左还是右
                            let sleft = Math.abs(recSize - ItemSizeMin);
                            let sright = Math.abs(ItemSizeMax - recSize);
                            // 若距离尺码区间左端点小于尺码区间右端点,则推荐尺码在尺码区间左边
                            if (sleft < sright) {
                                size_score = Math.round(
                                    ((ItemSizeMax - ItemSizeMin) /
                                        (ItemSizeMax - recSize)) *
                                        100
                                );
                            } else {
                                size_score = Math.round(
                                    ((ItemSizeMax - ItemSizeMin) /
                                        (recSize - ItemSizeMin)) *
                                        100
                                );
                            }
                        }
                    } else if (
                        this.$store.state.fitting.dataObj.fenlei ==
                            "Mountain bike" ||
                        this.$store.state.fitting.dataObj.fenlei == "Fitness bike"
                    ) {
                        if (
                            this.$store.state.fitting.dataObj.fenlei ==
                            "Fitness bike"
                        ) {
                            flag = true;
                            // 城市车计算方法
                            // 此项车辆尺码区间左端点
                            ItemSizeIMILeft = FitnessSize[ItemSizeMin][0];
                            // 此项车辆尺码区间右端点
                            ItemSizeIMIRight = FitnessSize[ItemSizeMax][1];
                        } else {
                            flag = false;
                            // 山地车计算方法
                            // 此项车辆尺码区间左端点
                            ItemSizeIMILeft = MountainSize[ItemSizeMin][0];
                            // 此项车辆尺码区间右端点
                            ItemSizeIMIRight = MountainSize[ItemSizeMax][1];
                        }

                        // 若此项车辆尺寸区间涵盖身高则得分100
                        if (
                            shengao > ItemSizeIMILeft &&
                            shengao < ItemSizeIMIRight
                        ) {
                            size_score = 100;
                            // flag:true 城市车
                            if (flag) {
                                if (shengao > 145 && shengao <= 155) {
                                    recSize = "XS";
                                } else if (shengao > 155 && shengao <= 165) {
                                    recSize = "S";
                                } else if (shengao > 165 && shengao <= 175) {
                                    recSize = "M";
                                } else if (shengao > 175 && shengao <= 183) {
                                    recSize = "L";
                                } else if (shengao > 183 && shengao <= 193) {
                                    recSize = "XL";
                                } else if (shengao > 193 && shengao <= 200) {
                                    recSize = "XXL";
                                }
                            } else {
                                // flag:false 山地车
                                if (shengao > 148 && shengao <= 157) {
                                    recSize = "XS";
                                } else if (shengao > 157 && shengao <= 165) {
                                    recSize = "S";
                                } else if (shengao > 165 && shengao <= 178) {
                                    recSize = "M";
                                } else if (shengao > 178 && shengao <= 185) {
                                    recSize = "L";
                                } else if (shengao > 185 && shengao <= 193) {
                                    recSize = "XL";
                                } else if (shengao > 193 && shengao <= 200) {
                                    recSize = "XXL";
                                }
                            }
                        } else {
                            // 不在尺码区间内,判断左还是右
                            let sleft = Math.abs(shengao - ItemSizeIMILeft);
                            let sright = Math.abs(ItemSizeIMIRight - shengao);
                            // 若距离尺码区间左端点小于尺码区间右端点,则身高在尺码区间左边
                            if (sleft < sright) {
                                size_score = Math.round(
                                    ((ItemSizeIMIRight - ItemSizeIMILeft) /
                                        (ItemSizeIMIRight - shengao)) *
                                        100
                                );
                                recSize = ItemSizeMin;
                            } else {
                                size_score = Math.round(
                                    ((ItemSizeIMIRight - ItemSizeIMILeft) /
                                        (shengao - ItemSizeIMILeft)) *
                                        100
                                );
                                recSize = ItemSizeMax;
                            }
                        }
                    }
                    // 给每项添加推荐尺码
                    item.size = recSize;

                    let score =
                        brand_score *
                            this.$store.state.fitting.dataObj.xuanze_three +
                        color_score * this.$store.state.fitting.dataObj.xuanze_two +
                        price_score * this.$store.state.fitting.dataObj.xuanze_one +
                        size_score;
                    itemScore[item.id] = score;
                });

                var keyArr = [],
                    valArr = [];
                for (var key in itemScore) {
                    keyArr.push(key);
                    valArr.push(itemScore[key]);
                }
                for (var i = 0, len = valArr.length; i < len; i++) {
                    for (var j = 0; j < len - i; j++) {
                        var keyTemp, valTemp;
                        if (valArr[j] < valArr[j + 1]) {
                            valTemp = valArr[j];
                            valArr[j] = valArr[j + 1];
                            valArr[j + 1] = valTemp;
                            keyTemp = keyArr[j];
                            keyArr[j] = keyArr[j + 1];
                            keyArr[j + 1] = keyTemp;
                        }
                    }
                }
                var sortArr = [];
                for (var i = 0; i < valArr.length; i++) {
                    sortArr.push(keyArr[i]);
                }
                // 得到评分降序数据
                sortArr.forEach((value) => {
                    this.$store.state.fitting.resData.forEach((item) => {
                        if (item.id == value) {
                            this.$store.state.fitting.newDataObj.push(item);
                            return;
                        }
                    });
                });
            },
        },
        activated() {
            this.$store.state.fitting.nowStep.isSecond = true;
            this.$store.state.fitting.nowStep.isThird = true;
            this.$store.state.fitting.nowStep.isFourth = false;
        },
        beforeRouteLeave(to, from, next) {
            this.isShownext = true;
            this.isloading = false;
            next();
        },
    };
</script>

<style scoped>
    #shengao,
    #kuagao {
        margin-top: 5px;
        background-color: rgba(255, 255, 255, 0);
        box-shadow: 0 2px 10px 0 rgba(0, 0, 0, 0.05);
    }
</style>