// 该文件用于创建路由器

import VueRouter from "vue-router";
import Fitting from '../pages/Fitting'
import Contact from '../pages/Contact'
import Product from '../pages/Product'
import StepFirst from '../pages/StepFirst'
import StepSecond from '../pages/StepSecond'
import StepThird from '../pages/StepThird'
import StepFourth from '../pages/StepFourth'
import Home from '../components/Home'

// 创建一个路由器 
const router = new VueRouter({
    // mode配置项:配置工作模式
    // hash模式(默认):#号后面的内容不会作为url发送给服务器,兼容性略好
    // histroy模式:没有#号,内容全会作为url发送给服务器,会出错,需要在服务器端进行相应配置才能使用
    mode: 'hash',

    // 路由规则
    routes: [
        // 配置一级路由
        {
            path:'/',
            component:Home
        },
        {
            path: '/Product',
            component: Product,
        },
        {
            path: '/Fitting',
            // 设置重定向：访问Fitting时自动跳转到StepFirst
            redirect:'/Fitting/stepfirst',
            component: Fitting,
            children: [
                {
                    name: 'stepfirst',
                    path: 'stepfirst',
                    component: StepFirst
                },
                {
                    name: 'stepsecond',
                    path: 'stepsecond',
                    component: StepSecond
                },
                {
                    name: 'stepthird',
                    path: 'stepthird',
                    component: StepThird
                },
                {
                    name: 'stepfourth',
                    path: 'stepfourth',
                    component: StepFourth
                },
            ]
        },
        {
            path: '/Contact',
            component: Contact,
        },
    ]
})

// // 全局前置路由守卫
// // 每一次路由切换之前 和初始化的时候,都会调用回调函数(用于审核权限,权限足够才跳转到相应组件)
