//         // 需要什么权限才能跳转
//         if (false) {
//             next()
//         } else {
//             alert('无权限！')
//         }
//     } else {
//         next()
//     }
// })

// // 全局后置路由守卫:跳转后调用回调函数
// router.afterEach((to, from) => {
//     document.title=to.meta.title || "Document"
// })

export default router