<template>
    <div>
        <div class="step">
            <div class="stepFirst">
                <div :class="stepDivFirst">1</div>
                <span>车辆样式</span>
            </div>
            <div class="stepLine" :class="stepLineFirst"></div>

            <div class="stepSecond">
                <div :class="stepDivSecond">2</div>
                <span>优先样式</span>
            </div>
            <div class="stepLine" :class="stepLineSecond"></div>

            <div class="stepThird">
                <div :class="stepDivThird">3</div>
                <span>个人数据</span>
            </div>
            <div class="stepLine" :class="stepLineThird"></div>

            <div class="stepFourth">
                <div :class="stepDivFourth">4</div>
                <span>推荐车辆</span>
            </div>
        </div>
        <div class="formBox">
            <keep-alive>
                <router-view></router-view>
            </keep-alive>
        </div>
        <Background />
    </div>
</template>

<script>
    import Background from "../components/Background";

    export default {
        name: "Fitting",
        computed: {
            stepDivFirst() {
                return {
                    stepActiveDiv: this.$store.state.fitting.nowStep.isFirst,
                };
            },
            stepDivSecond() {
                return {
                    stepActiveDiv: this.$store.state.fitting.nowStep.isSecond,
                };
            },
            stepDivThird() {
                return {
                    stepActiveDiv: this.$store.state.fitting.nowStep.isThird,
                };
            },
            stepDivFourth() {
                return {
                    stepActiveDiv: this.$store.state.fitting.nowStep.isFourth,
                };
            },
            stepLineFirst() {
                if (
                    this.$store.state.fitting.nowStep.isSecond ||
                    this.$store.state.fitting.nowStep.isThird ||
                    this.$store.state.fitting.nowStep.isFourth
                ) {
                    return {
                        stepEndLine: this.$store.state.fitting.nowStep.isFirst,
                    };
                } else {
                    return {
                        stepActiveLine: this.$store.state.fitting.nowStep.isFirst,
                    };
                }
            },
            stepLineSecond() {
                if (
                    this.$store.state.fitting.nowStep.isThird ||
                    this.$store.state.fitting.nowStep.isFourth
                ) {
                    return {
                        stepEndLine: this.$store.state.fitting.nowStep.isSecond,
                    };
                } else {
                    return {
                        stepActiveLine: this.$store.state.fitting.nowStep.isSecond,
                    };
                }
            },
            stepLineThird() {
                if (this.$store.state.fitting.nowStep.isFourth) {
                    return {
                        stepEndLine: this.$store.state.fitting.nowStep.isThird,
                    };
                } else {
                    return {
                        stepActiveLine: this.$store.state.fitting.nowStep.isThird,
                    };
                }
            },
            stepLineFourth() {
                return {
                    stepActiveLine: this.$store.state.fitting.nowStep.isFourth,
                };
            },
        },
        components: {
            Background,
        },
        created() {
            if (this.$route.path !== "/Fitting/stepfirst") {
                this.$router.replace({
                    path: "/Fitting/stepfirst",
                });
            }
        },
    };
</script>

<style>
    .step {
        z-index: 10;
        position: absolute;
        transform: translate(85%, 21%);
        width: 150px;
        height: 550px;
    }
    .step a {
        text-decoration: none;
    }
    .stepFirst,
    .stepSecond,
    .stepThird,
    .stepFourth {
        position: relative;
        text-align: center;
    }
    .step span {
        position: absolute;
        transform: translate(23px, -29px);
    }
    .stepFirst div,
    .stepSecond div,
    .stepThird div,
    .stepFourth div {
        margin: 5px auto;
        width: 30px;
        line-height: 32px;
        height: 30px;
        border-radius: 50%;
        background-color: #dadada;
    }
    .stepLine {
        margin: 5px auto;
        height: 120px;
        width: 2px;
        background-color: #dadada;
    }
    .stepActiveDiv {
        background-color: #2f86f6 !important;
        color: white !important;
    }
    .stepActiveLine {
        background: linear-gradient(#2f86f6, #dadada);
    }
    .stepEndLine {
        background-color: #2f86f6 !important;
    }
    .input-group-addon {
        margin-top: 0;
        background-color: rgba(255, 255, 255, 0);
    }
    .input-group {
        background-color: rgba(255, 255, 255, 0);
        box-shadow: 0 2px 10px 0 rgba(0, 0, 0, 0.05);
    }
    * {
        /* 初始化 */
        margin: 0;
        padding: 0;
    }
    .formBox {
        position: relative;
        width: 100%;
        height: 500px;
    }
    .form-container {
        left: 50%;
        transform: translate(-48%, 25%);
        position: absolute;
        z-index: 1;
        border-radius: 15px;
        width: 700px;
        height: 500px;
        background-color: rgba(255, 255, 255, 0);
        box-shadow: 0 5px 20px 4px rgba(0, 0, 0, 0.1);
        backdrop-filter: blur(5px);
    }

    .form-container .tit {
        width: 100%;
        text-align: center;
        font-size: 30px;
        margin: 60px auto 30px auto;
        letter-spacing: 3px;
    }

    .tijiao {
        width: 250px;
        height: 40px;
        position: absolute;
        left: 50%;
        bottom: 70px;
        transform: translatex(-50%);
        border: none;
        background: linear-gradient(-200deg, #fac0e7, #aac2ee);
        color: #fff;
        font-weight: bold;
        letter-spacing: 3px;
        border-radius: 10px;
        cursor: pointer;
        /* 动画过渡 */
        transition: 0.5s;
    }
    .tijiao:hover {
        background: linear-gradient(-200deg, #aac2ee, #fac0e7);
        background-position-x: -280px;
    }
    label {
        letter-spacing: 1px;
        font-size: 16px;
    }
    .btn {
        position: absolute;
        right: 72px;
        bottom: 60px;
        padding: 0;
    }
    .return {
        position: absolute;
        top: 10px;
        left: 15px;
    }
</style>