<template>
    <div class="form-container">
        <div class="tit">请选择您预期的车辆样式</div>
        <form action="/productApp/form" method="post">
            <div style="float: left; width: 180px; margin-left: 67px">
                <label for="fenlei" style="margin-top: 20px">分类</label>
                <select
                    id="fenlei"
                    name="fenlei"
                    class="form-control"
                    v-model="$store.state.fitting.dataObj.fenlei"
                >
                    <option>Road bike</option>
                    <option>Mountain bike</option>
                    <option>TT bike</option>
                    <option>Fitness bike</option>
                </select>
            </div>

            <div style="float: left; width: 180px; margin-left: 20px">
                <label for="pinpai" style="margin-top: 20px">品牌</label>
                <select
                    id="pinpai"
                    name="pinpai"
                    class="form-control"
                    v-model="$store.state.fitting.dataObj.pinpai"
                >
                    <option>Colnago</option>
                    <option>Specialized</option>
                    <option>Trek</option>
                    <option>Pinarello</option>
                    <option>Cervelo</option>
                    <option>Canyon</option>
                    <option>Look</option>
                    <option>Scott</option>
                    <option>Ridley</option>
                    <option>Focus</option>
                    <option>lapierre</option>
                    <option>Factor</option>
                    <option>Felt</option>
                    <option>derosa</option>
                    <option>Cannondale</option>
                    <option>Cube</option>
                    <option>Bmc</option>
                    <option>Ceepo</option>
                    <option>Bh</option>
                    <option>bianchi</option>
                    <option>orbea</option>
                    <option>marin</option>
                    <option>Argon18</option>
                    <option>Decathlon</option>
                    <option>Fuji</option>
                    <option>Time</option>
                    <option>Wilier</option>
                    <option>Storck</option>
                    <option>Kuota</option>
                    <option>brompton</option>
                    <option>Voice</option>
                    <option>Elghteen</option>

                    <option>Giant</option>
                    <option>Merida</option>
                    <option>Xds</option>
                    <option>Trinx</option>
                    <option>Kung</option>
                    <option>Zgl</option>
                    <option>Quick</option>
                    <option>Rollingstone</option>
                    <option>Java</option>
                    <option>Sava</option>
                    <option>Twitter</option>
                    <option>Camp</option>
                    <option>Missile</option>
                    <option>Pardus</option>
                    <option>Winspace</option>
                    <option>Motion</option>
                    <option>Ucc</option>
                    <option>Bigrock</option>
                    <option>Swift</option>
                    <option>Tropix</option>
                    <option>Gusto</option>
                    <option>Sunpeed</option>
                    <option>Battle</option>
                    <option>Seaboard</option>
                    <option>Avenger</option>
                    <option>Upland</option>

                    <option>Other...</option>
                </select>
            </div>

            <div style="float: left; width: 180px; margin-left: 20px">
                <label for="tuzhuang" style="margin-top: 20px">涂装</label>
                <input
                    type="color"
                    id="tuzhuang"
                    name="tuzhuang"
                    class="form-control"
                    v-model="$store.state.fitting.dataObj.tuzhuang"
                />
            </div>

            <div class="jiage">
                <label for="jiage" style="margin-top: 35px; margin-bottom: 10px"
                    >价格区间</label
                >
                <span>/k</span>
                <div class="jiagebox">
                    <el-slider
                        v-model="$store.state.fitting.dataObj.jiage"
                        range
                        :min='1'
                        :max="80"
                    >
                    </el-slider>
                </div>
            </div>
            <div class="btn">
                <el-button type="danger" plain v-show="isShowerr" @click="err"
                    >填写错误</el-button
                >
                <el-button
                    type="success"
                    plain
                    @click="next"
                    v-show="isShownext"
                    style="margin-left: 0"
                    >下一步</el-button
                >
            </div>
        </form>
    </div>
</template>

<script>
    export default {
        name: "StepFirst",
        data() {
            return {
                that: this,
                errMsg: "",
            };
        },
        computed: {
            isShownext() {
                this.errMsg = "";
                if (
                    this.$store.state.fitting.dataObj.fenlei == "" ||
                    this.$store.state.fitting.dataObj.tuzhuang == "" ||
                    this.$store.state.fitting.dataObj.jiage == "" ||
                    this.$store.state.fitting.dataObj.pinpai == ""
                ) {
                    if (this.$store.state.fitting.dataObj.fenlei == "") {
                        this.errMsg += "“分类”";
                    }
                    if (this.$store.state.fitting.dataObj.tuzhuang == "") {
                        this.errMsg += "“涂装”";
                    }
                    if (this.$store.state.fitting.dataObj.jiage == "") {
                        this.errMsg += "“价格”";
                    }
                    if (this.$store.state.fitting.dataObj.pinpai == "") {
                        this.errMsg += "“品牌”";
                    }
                    return false;
                } else {
                    return true;
                }
            },
            isShowerr() {
                return !this.isShownext;
            },
        },
        methods: {
            err() {
                this.$notify.error({
                    title: "错误",
                    message: `${this.errMsg}：不能为空！`,
                });
            },
            next() {
                this.$router.push({
                    name: "stepsecond",
                });
            },
        },
        activated() {
            this.$store.state.fitting.nowStep.isSecond = false;
            this.$store.state.fitting.nowStep.isThird = false;
            this.$store.state.fitting.nowStep.isFourth = false;
        },
    };
</script>

<style scoped>
    #fenlei,
    #tuzhuang,
    #pinpai {
        margin-top: 5px;
        background-color: rgba(255, 255, 255, 0);
        box-shadow: 0 2px 10px 0 rgba(0, 0, 0, 0.05);
    }
    .jiage {
        width: 572px;
        float: left;
        margin-left: 67px;
    }
    .jiagebox {
        width: 100%;
        border: 1px solid #ccc;
        height: 50px;
        background-color: rgba(255, 255, 255, 0);
        box-shadow: 0 2px 10px 0 rgb(0 0 0 / 5%);
        padding: 6px 22px;
        border-radius: 4px;
    }
</style>