<script>
    import Footer from "../components/Footer.vue";
    export default {
        name: "Product",
        data() {
            return {
                isfirstShow: "display:block",
                issecondShow: "display:none",
                isthirdShow: "display:none",
            };
        },
        methods: {
            firstShow() {
                this.isfirstShow = "display:block";
                this.issecondShow = "display:none";
                this.isthirdShow = "display:none";
            },
            secondShow() {
                this.isfirstShow = "display:none";
                this.issecondShow = "display:block";
                this.isthirdShow = "display:none";
            },
            thirdShow() {
                this.isfirstShow = "display:none";
                this.issecondShow = "display:none";
                this.isthirdShow = "display:block";
            },
        },
        mounted() {
            $(window).scroll(function () {
                //只要窗口滚动,就触发下面代码
                var scrollt =
                    document.documentElement.scrollTop + document.body.scrollTop; //获取滚动后的高度
                $(".back").click(function () {
                    // 因为是元素做动画, 此处返顶动画的实现元素应该是body或者html
                    $("body,html").stop().animate({
                        scrollTop: 0,
                    });
                });
                if (scrollt > 10) {
                    //判断滚动后高度超过,就显示
                    $(".colnago").fadeIn(1500);
                } else {
                    $(".colnago").stop().fadeOut(0);
                }
                if (scrollt > 30) {
                    //判断滚动后高度超过1px,就显示
                    $(".xuangou").fadeIn(10);
                } else {
                    $(".xuangou").stop().fadeOut(10); //如果返回或者没有超过,就淡出.必须加上stop()停止之前动画,否则会出现闪动
                }
                if (scrollt > 850) {
                    //判断滚动后高度超过,就显示
                    $("#suolue").fadeIn(1500);
                } else {
                    $("#suolue").stop().fadeOut(0);
                }
                if (scrollt > 1200) {
                    //判断滚动后高度超过,就显示
                    $("#zhanshi").fadeIn(1700);
                    $(".back").fadeIn(1000);
                } else {
                    $("#zhanshi").stop().fadeOut(0);
                    $(".back").stop().fadeOut(0);
                }
                if (scrollt > 1300) {
                    //判断滚动后高度超过,就显示
                    $(".gallery-container").show(2000);
                } else {
                    $(".gallery-container").stop().hide(0);
                }
                if (scrollt > 2500) {
                    //判断滚动后高度超过,就显示
                    $("#suoxie-s").fadeIn(2000);
                } else {
                    $("#suoxie-s").stop().fadeOut(0);
                }
            });

            // 图片层叠切换js
            var _createClass = (function () {
                function defineProperties(target, props) {
                    for (var i = 0; i < props.length; i++) {
                        var descriptor = props[i];
                        descriptor.enumerable = descriptor.enumerable || false;
                        descriptor.configurable = true;
                        if ("value" in descriptor) descriptor.writable = true;
                        Object.defineProperty(target, descriptor.key, descriptor);
                    }
                }
                return function (Constructor, protoProps, staticProps) {
                    if (protoProps)
                        defineProperties(Constructor.prototype, protoProps);
                    if (staticProps) defineProperties(Constructor, staticProps);
                    return Constructor;
                };
            })();
            function _classCallCheck(instance, Constructor) {
                if (!(instance instanceof Constructor)) {
                    throw new TypeError("Cannot call a class as a function");
                }
            }
            var placeholderTwo = function placeholderTwo(selector) {
                return document.querySelector(selector);
            };
            var placeholderOne = function placeholderOne(selector) {
                return document.querySelectorAll(selector);
            };

            function lerp(n1, n2, speed) {
                return (1 - speed) * n1 + speed * n2;
            }

            function angle(from, to) {
                return Math.atan2(to[1] - from[1], to[0] - from[0]);
            }

            function distNorm(from, to, xMax, yMax) {
                return Math.sqrt(
                    Math.pow((to[0] - from[0]) / xMax, 2),
                    Math.pow((to[1] - from[1]) / yMax, 2)
                );
            }

            Array.prototype.lerp = function (target, speed) {
                var _this = this;
                this.forEach(function (n, i) {
                    return (_this[i] = lerp(n, target[i], speed));
                });
            };
            var Frame = (function () {
                function Frame(node) {
                    _classCallCheck(this, Frame);
                    this.node = node;
                    this.scale = 1;
                    this.maxScale = 1.25;
                    this.rotation = [0, 0, 0];
                    this.translation = [0, 0, 0];
                    this.center = [0, 0];
                    this.target = [
                        0.5 * window.innerWidth,
                        0.5 * window.innerHeight,
                    ];

                    this.padding = [
                        0.5 * this.node.clientWidth,
                        0.5 * this.node.clientHeight,
                    ];

                    this.focus = false;
                    this.mouseover = false;
                    this.distance = 0;
                    this.node.addEventListener("mousemove", this.hover.bind(this));
                    this.node.addEventListener("mouseleave", this.hover.bind(this));
                    this.setCenter();
                }
                _createClass(Frame, [
                    {
                        key: "setCenter",
                        value: function setCenter() {
                            var rect = this.node.getBoundingClientRect();
                            this.center[0] = rect.left + this.padding[0];
                            this.center[1] = rect.top + this.padding[1];
                            return this;
                        },
                    },
                    {
                        key: "setTarget",
                        value: function setTarget(target) {
                            this.target[0] = target[0];
                            this.target[1] = target[1];
                            return this;
                        },
                    },
                    {
                        key: "setDistance",
                        value: function setDistance() {
                            this.distNorm = distNorm(
                                this.center,
                                this.target,
                                window.innerWidth,
                                0.5 * window.innerHeight
                            );
                            return this;
                        },
                    },
                    {
                        key: "translate",
                        value: function translate() {
                            this.translation.lerp(
                                [
                                    0,
                                    0,
                                    this.mouseover
                                        ? 300
                                        : 200 - this.distNorm * 400,
                                ],
                                0.15
                            );
                            return this;
                        },
                    },
                    {
                        key: "rotate",
                        value: function rotate() {
                            var theta = angle(this.center, this.target);
                            this.rotation.lerp(
                                [
                                    Math.sin(-theta) * 60 * this.distNorm,
                                    Math.cos(theta) * 90 * this.distNorm,
                                ],
                                0.15
                            );
                            return this;
                        },
                    },
                    {
                        key: "update",
                        value: function update() {
                            this.node.style.transform =
                                "\n\t\t\ttranslate3d(" +
                                this.translation[0] +
                                "px," +
                                this.translation[1] +
                                "px," +
                                this.translation[2] +
                                "px) \n\t\t\trotateX(" +
                                this.rotation[0] +
                                "deg) rotateY(" +
                                this.rotation[1] +
                                "deg)\n\t\t";
                        },
                    },
                    {
                        key: "hover",
                        value: function hover(e) {
                            this.mouseover = e.type === "mousemove";
                        },
                    },
                ]);
                return Frame;
            })();
            var Gallery = (function () {
                function Gallery() {
                    _classCallCheck(this, Gallery);
                    this.container = placeholderTwo(".gallery");
                    this.center = [
                        0.5 * window.innerWidth,
                        0.5 * window.innerHeight,
                    ];

                    this.mouse = this.center.slice(0);
                    this.target = this.mouse.slice(0);
                    this.container.addEventListener(
                        "mousemove",
                        this.hover.bind(this)
                    );
                    this.container.addEventListener(
                        "mouseleave",
                        this.hover.bind(this)
                    );
                    window.addEventListener("resize", this.resize.bind(this));
                    this.initFrames();
                    this.update();
                }
                _createClass(Gallery, [
                    {
                        key: "initFrames",
                        value: function initFrames() {
                            var _this2 = this;
                            this.frames = [];
                            placeholderOne(".frame").forEach(function (node) {
                                return _this2.frames.push(new Frame(node));
                            });
                        },
                    },
                    {
                        key: "resize",
                        value: function resize() {
                            this.center = [
                                0.5 * window.innerWidth,
                                0.5 * window.innerHeight,
                            ];

                            this.frames.forEach(function (frame) {
                                return frame.setCenter();
                            });
                        },
                    },
                    {
                        key: "hover",
                        value: function hover(e) {
                            this.mouseover = e.type === "mousemove";
                            this.target[0] = e.clientX;
                            this.target[1] = e.clientY;
                        },
                    },
                    {
                        key: "update",
                        value: function update() {
                            var _this3 = this;
                            this.mouse.lerp(
                                this.mouseover ? this.target : this.center,
                                0.125
                            );

                            this.frames.forEach(function (frame) {
                                frame
                                    .setTarget(_this3.mouse)
                                    .setDistance()
                                    .translate()
                                    .rotate()
                                    .update();
                            });
                            this.container.style.perspectiveOrigin =
                                this.mouse[0] + "px 50%";
                            window.requestAnimationFrame(this.update.bind(this));
                        },
                    },
                ]);
                return Gallery;
            })();

            new Gallery();
        },
        components: {
            Footer,
        },
    };
    // 固定高度显示按钮
</script>

