<template>
    <div class="form-container">
        <div class="tit">请输入您对以下属性的重视度</div>
        <form action="/productApp/form" method="post">
            <div style="float: left; width: 580px; margin-left: 87px">
                <div style="padding-left: 0">
                    <label>注意：三者重视度合计应为1</label>
                    <div class="first">
                        <span>价格</span>
                        <el-slider
                            v-model="$store.state.fitting.dataObj.xuanze_one"
                            :step="0.1"
                            :max="1"
                            show-input
                        >
                        </el-slider>
                    </div>
                    <div class="second">
                        <span>涂装</span>
                        <el-slider
                            v-model="$store.state.fitting.dataObj.xuanze_two"
                            :step="0.1"
                            :max="1"
                            show-input
                        >
                        </el-slider>
                    </div>
                    <div class="third">
                        <span>品牌</span>
                        <el-slider
                            v-model="$store.state.fitting.dataObj.xuanze_three"
                            :step="0.1"
                            :max="1"
                            show-input
                        >
                        </el-slider>
                    </div>
                </div>
            </div>
            <div class="btn">
                <el-button type="danger" plain v-show="isShowerr" @click="err"
                    >填写错误</el-button
                >
                <el-button
                    type="success"
                    plain
                    @click="next"
                    v-show="isShownext"
                    style="margin-left: 0"
                    >下一步</el-button
                >
            </div>
            <div class="return">
                <el-button icon="el-icon-back" circle @click="ret"></el-button>
            </div>
        </form>
    </div>
</template>

<script>
    export default {
        name: "StepFirst",
        data() {
            return {
                that: this,
            };
        },
        computed: {
            isShownext() {
                if (
                    this.$store.state.fitting.dataObj.xuanze_one *10+
                        this.$store.state.fitting.dataObj.xuanze_two *10+
                        this.$store.state.fitting.dataObj.xuanze_three *10==
                    10
                ) {
                    return true;
                } else {
                    return false;
                }
            },
            isShowerr() {
                return !this.isShownext;
            },
        },
        methods: {
            err() {
                this.$notify.error({
                    title: "错误",
                    message: "三者重视度合计应为1",
                });
            },
            next() {
                this.$router.push({
                    name: "stepthird",
                });
            },
            ret() {
                this.$router.push({
                    name: "stepfirst",
                });
            },
        },
        activated() {
            this.$store.state.fitting.nowStep.isSecond = true;
            this.$store.state.fitting.nowStep.isThird = false;
            this.$store.state.fitting.nowStep.isFourth = false;
        },
    };
</script>

<style scoped lang='less'>
    .first,
    .second,
    .third {
        height: 50px;
        width: 80%;
        margin-left: 35px;
        margin-top: 18px;
        p {
            float: left;
        }
    }
</style>